const Modal = ({
  modalStatus, //-state
  closeModal, //state
  closeUpToDelayMilliSeconds = 0, //state
  closeBefore = () => {}, //state
  modalWrapStyle = "", //
  contentsWrapStyle = "", //
  contentsElm = <></>, //
  closeButtonWrapStyle = "", //
  closeButtonElm = <></>, //
  overlayStyle = "", //
}) => {
  if (!modalStatus) return <></>;
  return (
    <>
      <div className={modalWrapStyle}>
        {/* contents */}
        <div className={contentsWrapStyle}>
          <div
            className={closeButtonWrapStyle}
            onClick={() => {
              closeBefore();
              setTimeout(closeModal, closeUpToDelayMilliSeconds);
            }}
          >
            {closeButtonElm}
          </div>
          {contentsElm}
        </div>

        {/* closeButton */}
        <div
          className={overlayStyle}
          onClick={() => {
            closeBefore();
            setTimeout(closeModal, closeUpToDelayMilliSeconds);
          }}
        ></div>
      </div>
    </>
  );
};

export default Modal;
